@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  min-height: 100vh;
  background-color: #8a2be2;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 500px;
  min-height: 150px;
  background-color: #fff;
  border-radius: 20px;
  flex-direction: column;
  padding: 20px;
  box-shadow: 0 0 40px #0009;
}

h1 {
  text-align: center;
  color: #8a2be2;
  text-transform: uppercase;
  text-shadow: 1px 1px #000;
  margin-bottom: 1.5rem;
  font-size: 3rem;
}

.search-container {
  grid-template-columns: 9fr 3fr;
  gap: 2rem;
  display: grid;
}

input {
  border: none;
  border-bottom: 2px solid #8a2be2;
  outline: none;
  padding: 5px 10px;
  font-weight: 500;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #8a2be2;
  border: none;
  border-radius: 2rem;
}

.result {
  margin-top: 10px;
}

.result .flag-img {
  width: 45%;
  min-width: 7.5em;
  margin: 1.8em auto 1.2em;
  display: block;
}

.result h2 {
  color: #222a43;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 1.8em;
  font-weight: 600;
}

.result .data-wrapper {
  letter-spacing: .3px;
  margin-bottom: 1em;
}

.result h4 {
  color: #222a43;
  font-weight: 500;
  display: inline;
}

.result span {
  color: #5d6274;
}

.result h3 {
  text-align: center;
  color: #ff465a;
  margin-top: 10px;
  font-size: 1.2em;
  font-weight: 400;
}

.result .loading {
  color: #000;
  text-align: center;
  margin-top: 10px;
  margin-bottom: -10px;
  font-size: 15px;
}

